import React, { Component } from 'react';
import '../CSS/Layout.css';
class NavTop extends Component {
    render() {
        return (
            <div>
                <div>
                    <div className="bm-overlay" style={{ position: 'fixed', zIndex: 1000, width: '100%', height: '100%', background: 'rgba(0, 0, 0, 0.3)', opacity: 0, transition: 'opacity 0.3s ease 0s, transform 0s ease 0.3s', transform: 'translate3d(100%, 0px, 0px)' }} />
                    <div  className="bm-menu-wrap" style={{ position: 'fixed', right: 'inherit', zIndex: 1100, width: '100vw', height: '100%', transition: 'all 0.5s ease 0s', transform: 'translate3d(-100%, 0px, 0px)' }}>
                        <div className="bm-menu" style={{ height: '100%', boxSizing: 'border-box', overflow: 'auto' }}>
                            <nav className="bm-item-list" style={{ height: '100%' }}>
                                <div className="sidebar-container" style={{ background: 'white', width: '100vw', height: '100vh' }}>
                                    <div className="sidebar-body" style={{ background: 'white', width: '100vw', height: '100vh' }}>
                                        <ul>
                                            <li><a>Đăng nhập</a></li>
                                            <li><a href="/signup">Đăng kí</a></li>
                                        </ul>
                                    </div>
                                    <div />
                                </div>
                            </nav>
                        </div>
                        <div>
                            <div className="bm-cross-button" style={{ position: 'absolute', width: 24, height: 24, right: 8, top: 8 }}><span style={{ position: 'absolute', top: 6, right: 14 }}><span className="bm-cross" style={{ position: 'absolute', width: 3, height: 14, transform: 'rotate(45deg)' }} /><span className="bm-cross" style={{ position: 'absolute', width: 3, height: 14, transform: 'rotate(-45deg)' }} /></span><button tabIndex={-1} style={{ position: 'absolute', left: 0, top: 0, width: '100%', height: '100%', margin: 0, padding: 0, border: 'none', fontSize: 0, background: 'transparent', cursor: 'pointer' }}>Close Menu</button></div>
                        </div>
                    </div>
                    <div>
                        <div className="bm-burger-button" style={{ zIndex: 1000 }}><span><span className="bm-burger-bars" style={{ position: 'absolute', height: '20%', left: 0, right: 0, top: '0%', opacity: 1 }} /><span className="bm-burger-bars" style={{ position: 'absolute', height: '20%', left: 0, right: 0, top: '40%', opacity: 1 }} /><span className="bm-burger-bars" style={{ position: 'absolute', height: '20%', left: 0, right: 0, top: '80%', opacity: 1 }} /></span><button style={{ position: 'absolute', left: 0, top: 0, width: '100%', height: '100%', margin: 0, padding: 0, border: 'none', fontSize: 0, background: 'transparent', cursor: 'pointer' }}>Open Menu</button></div>
                    </div>
                </div>
                <section className="header">
                    <div className="header-container">
                        <div className="header-icon">
                            <div className="js-toggle-right-slidebar js-menu-mobile">
                                <div className="trigger-menu"><span className="three-bars-icon" /></div>
                            </div>
                            <div>
                                <div />
                            </div>
                            <a className="item-app show-on-med-and-down" href="/howitwork"><i className="ic ic-info-wh" /></a>
                        </div>
                        <div className="logo-container"><a href="/">abc</a></div>
                        <div className="info-container"><span><i className="ic ic-phone" /><a href="tel:19009217" style={{ color: 'white' }}>1900 9217</a></span><span><i className="ic ic-email" /> contact@mioto.vn</span><span><a href="https://www.messenger.com/t/mioto.vn" target="_blank" style={{ color: 'white' }}><i className="ic ic-messenger" /> Facebook</a></span></div>
                        <div className="menu-container">
                            <ul>
                                <li className="has-magr"><a href="/howitwork"><i className="ic ic-info-wh" /> Hướng dẫn</a></li>
                                <li><a>Đăng nhập</a></li>
                                <li><a className="btn btn-transparent btn--m" href="/signup">Đăng kí</a></li>
                            </ul>
                            <div />
                        </div>
                    </div>
                </section>
            </div>


        );
    }
}

export default NavTop;