import React, { Component } from 'react'
import ReactDOM from 'react-dom';


/* Default position */
const defaultPosition = {
  lat: 27.9878,
  lng: 86.9250
};


export default class LocationPicker extends Component {

  constructor(props){
      super(props);

      this.state = {
        address: "Nhật bản",
        position:{},
       defaultPosition: defaultPosition
      };

      //Blind
      this.handleLocationChange = this.handleLocationChange.bind(this);
  }


  
  handleLocationChange({position, address}){

    // Set new location if yous position changes or position updates more accurately 
    this.setState({position, address})
  }

  componentDidMount () {
    navigator && navigator.geolocation.getCurrentPosition(position => {
      const { latitude, longitude } = position.coords;
      this.setState({
        defaultPosition: {
          lat: latitude,
          lng: longitude
        }
      });
    });
  }

  

  render() {
  //  console.log(this.props.defaultPosition);
    return (
      <div>
        <div>
          <h1>{this.state.address}</h1>
           <h2 defaultPosition = {this.props.defaultPosition}></h2>
           {/* onChange={this.handleLocationChange} */}
            <button id="find-me">Show my location</button><br />
            <button onChange={this.handleLocationChange}>Update changes location</button><br />
            <p id="status" />
            <a id="map-link" target="_blank" />
            {/* <LocationPicker
            containerElement={ <div style={ {height: '100%'} } /> }
            mapElement={ <div style={ {height: '400px'} } /> }
            defaultPosition={this.state.defaultPosition}
            radius={-1}
            onChange={this.handleLocationChange}
          /> */}
        </div>
      </div>
    )
  }
}
