// import logo from '../logo.svg';
import '../App.css';

import React, { Component } from 'react';
import gpl from 'graphql-tag';
import { graphql } from 'react-apollo';

const AppQuery = gpl`
  query {
    animals {
      name
    }
  }
`;

class Home extends Component {
    render() {
        let { loading, animals } = this.props
        animals = animals ? animals : []
        if (loading) {
            return <p>Loading...</p>;
        }
        return (
            <div className='App'>
                {animals.map((animal, index) => {
                    return (
                        <p key={index}>
                            {animal.name}
                        </p>
                    );
                })}
            </div>
        );
    }
}

export default graphql(AppQuery)(Home);


